import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Fpas } from '../../../../models/dtos/mfp/fpas';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface FpasEditViewProps extends EditViewProps<Fpas> {}

const FpasEditView: FC<FpasEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const selecteItem = getSelectedItem(props);
  const formProps = useForm<Fpas>({ defaultValues: { ...selecteItem }, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Códigos de FPAS"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 6, validation: /[0-9]/ } }}
              isPrependLabel={true}
              id="codigo"
              label="Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 500 } }}
              isPrependLabel={true}
              id="descricao"
              label="Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default FpasEditView;
